@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }
@keyframes loading {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }

@-webkit-keyframes fadeIn {
  0% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8); }
  60% {
    opacity: 1;
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8); }
  60% {
    opacity: 1;
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }
  100% {
    opacity: 1; } }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-size: 62.5%; }

body {
  font-family: "Montserrat" ,Arial, Helvetica, sans-serif;
  background: white;
  font-size: 1.6rem; }

.app {
  margin: 5rem 0; }

.loading {
  display: inline-block;
  border: 10px solid #eee;
  border-bottom: 10px solid grey;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  -webkit-animation: loading 1.4s linear infinite;
          animation: loading 1.4s linear infinite;
  position: absolute;
  top: 25rem;
  left: 12rem; }
  @media (min-width: 769px) {
    .loading {
      top: 35%;
      left: 45%; } }

.header {
  text-align: center;
  margin: 1rem 0; }
  @media (min-width: 769px) {
    .header {
      width: 80%;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center; } }

.heading {
  font-size: 1.7rem; }
  @media (min-width: 769px) {
    .heading {
      font-size: 2.4rem; } }

.search-field {
  padding: 2rem;
  font-size: 2rem;
  background-color: transparent;
  width: 90%;
  border-radius: 5px;
  border: 1px solid #a5afd7;
  margin: 1rem 0; }
  .search-field:focus {
    outline: none; }
  @media (min-width: 769px) {
    .search-field {
      width: 40%;
      padding: 1rem;
      font-size: 1.3rem; } }

.employee-list {
  margin: 0 auto;
  display: grid; }
  @media (min-width: 769px) {
    .employee-list {
      width: 80%;
      grid-template-columns: repeat(3, 1fr); } }
  @media (max-width: 769px) and (min-width: 480px) {
    .employee-list {
      grid-template-columns: repeat(2, 1fr); } }

.employee-card {
  border: 1px solid #a5afd7;
  background-color: rgba(255, 255, 255, 0.5);
  flex-basis: 300px;
  display: flex;
  align-items: center;
  padding: 1rem;
  border-radius: 5px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
  flex-basis: 25%;
  margin: 1rem;
  cursor: pointer; }
  .employee-card:hover {
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.4); }

.avatar {
  width: 35%;
  border-radius: 50%; }

.info {
  margin: 0 1rem;
  text-align: left; }

.name {
  font-size: 2rem;
  font-weight: bold;
  text-transform: capitalize;
  color: #777; }

.email {
  font-size: 1.1rem; }
  @media (min-width: 769px) {
    .email {
      font-size: 1.2rem;
      margin: .5rem 0; } }

.extra {
  font-size: 1.3rem;
  color: #a5afd7; }
  .extra .city {
    margin: 10px 0;
    text-transform: capitalize; }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center; }

.modal {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
  padding: 1rem;
  width: 100%;
  margin: 0 1rem;
  position: relative; }
  .modal:focus {
    outline: none; }
  @media (min-width: 769px) {
    .modal {
      opacity: 0;
      margin-bottom: 20rem;
      width: 25%;
      -webkit-animation: fadeIn .4s ease-in-out forwards;
              animation: fadeIn .4s ease-in-out forwards; } }

.employee_modal {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding: 0 2rem; }
  @media (orientation: portrait), (min-width: 769px) {
    .employee_modal {
      flex-direction: column; } }

.modal_avatar {
  border-radius: 50%;
  width: 15rem;
  display: inline-block; }

.modal_info {
  padding: 1rem;
  color: #777; }
  .modal_info-name {
    text-transform: capitalize;
    font-size: 2rem;
    color: black; }
  .modal_info-city {
    text-transform: capitalize; }
  .modal_info p {
    margin: .5rem 0; }
  .modal_info-email {
    font-size: 1.3rem; }
  .modal_info-extra {
    max-width: 100%;
    margin: 1rem 0;
    padding: 1rem 0;
    border-top: 1px solid #777; }

.left, .right {
  font-size: 3rem;
  font-weight: bold;
  color: black;
  cursor: pointer; }

.left {
  position: absolute;
  top: 45%;
  left: .1rem; }

.right {
  position: absolute;
  top: 45%;
  right: .1rem; }

.close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: black;
  cursor: pointer;
  font-size: 2rem; }

.ReactModalPortal > div {
  opacity: 0; }

.ReactModalPortal .ReactModal__Overlay {
  transition: opacity 200ms ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center; }

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1; }

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0; }

